* {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

body{
  padding: 60px;    
}

input {
  width: 100%;
  font-size: 1.2rem;
  color: gray;
}


.grid-item-item{
  text-align: center;
  border: 1px solid red;
  overflow: hidden;
  padding: 16px;
  margin: 8px;
}

.grid-item-item img{
  max-height: 160px;
}

.grid{
  display: flex;
  flex-wrap: wrap;
}